import { useState, useEffect, useRef } from 'react'
import { isEqual, debounce } from 'lodash-es'

import { RESIZE_WINDOW_DELAY } from './constants'

type AnchorElRect = DOMRect | null | undefined
type AnchorEl = HTMLElement | null | undefined

export const useAnchorElRect = (anchorEl: AnchorEl): AnchorElRect => {
  const anchorElRef = useRef<AnchorEl>(anchorEl)
  const anchorRectRef = useRef<AnchorElRect>(anchorEl?.getBoundingClientRect())
  const [anchorRect, setAnchorRect] = useState<AnchorElRect>(anchorEl?.getBoundingClientRect())

  useEffect(() => {
    anchorElRef.current = anchorEl
  }, [anchorEl])

  useEffect(() => {
    const resizeCb = debounce(() => {
      const updatedAnchorRect = anchorElRef.current?.getBoundingClientRect()

      if (!isEqual(anchorRectRef.current, updatedAnchorRect)) {
        anchorRectRef.current = updatedAnchorRect
        setAnchorRect(updatedAnchorRect)
      }
    }, RESIZE_WINDOW_DELAY)

    window.addEventListener('resize', resizeCb)

    return () => {
      window.removeEventListener('resize', resizeCb)
    }
  }, [])

  return anchorRect
}
