import { useMemo, useRef, useState, useEffect } from 'react'

import useClickOutside from '@hooks/useClickOutside'
import type { UseOutsideClickConfig } from '@/hooks'

import { calculateConfig, calculateConfigWithoutAnchor } from './utils'
import type { ConfigProps, Coords, PlacementProps } from './types'
import { useAnchorElRect } from './useAnchorElRect'
import {
  DEFAULT_PLACEMENT,
  DEFAULT_TRANSFORM,
  OUTSIDE_CLICK_CONFIG,
  DEFAULT_WITHOUT_ANCHOR_PLACEMENT,
  noop
} from './constants'

export interface UsePopoverProps {
  onClose?: () => void
  placement?: Partial<PlacementProps>
  transform?: Partial<Coords>
  maxHeight?: 'auto' | number
  minWidth?: number
  anchorEl?: HTMLElement | null
  outsideClickConfig?: UseOutsideClickConfig
}

interface UsePopoverReturn {
  popover: HTMLDivElement
  config: ConfigProps
}

export const usePopover = ({
  onClose = noop,
  transform,
  placement,
  anchorEl,
  maxHeight,
  minWidth,
  outsideClickConfig = {}
}: UsePopoverProps): UsePopoverReturn => {
  const portalRef = useRef(document.createElement('div'))
  const [portalWidth, setPortalWidth] = useState<number>(0)

  useClickOutside(portalRef, onClose, { ...OUTSIDE_CLICK_CONFIG, ...outsideClickConfig })

  useEffect(() => {
    document.body.appendChild(portalRef.current)

    const { width } = portalRef.current.getBoundingClientRect()
    setPortalWidth(width)

    return () => {
      document.body.removeChild(portalRef.current)
    }
  }, [])

  const anchorRect = useAnchorElRect(anchorEl)

  const config = useMemo(() => {
    return anchorRect == null
      ? calculateConfigWithoutAnchor({
        ...DEFAULT_TRANSFORM,
        ...transform,
        ...DEFAULT_WITHOUT_ANCHOR_PLACEMENT,
        ...placement,
        maxHeight,
        minWidth
      })
      : calculateConfig({
        ...DEFAULT_TRANSFORM,
        ...transform,
        ...DEFAULT_PLACEMENT,
        ...placement,
        anchorRect,
        maxHeight,
        minWidth,
        portalWidth
      })
  }, [
    maxHeight,
    placement,
    transform,
    portalWidth,
    anchorRect,
    minWidth
  ])

  return {
    popover: portalRef.current,
    config
  }
}
