import type { FC } from 'react'
import { useMemo } from 'react'
import { createPortal } from 'react-dom'
import { cn } from '@utils/ui'

import type { UsePopoverProps } from './hook'
import { usePopover } from './hook'
import { useAnimatedToggle } from './hook/useAnimatedToggler'
import { createRootStyles, ANIMATED_POPOVER_STYLE } from './styles'

export interface PopoverCbProps extends UsePopoverProps {
  className?: string
}

export interface PopoverProps extends Omit<PopoverCbProps, 'anchorEl'> {
  anchorEl?: HTMLElement | null
  open: boolean
}

const PopoverHOC = (Popover: FC<PopoverCbProps>): FC<PopoverProps> => ({
  open,
  className,
  ...otherProps
}) => {
  const { isOpen, isAnimatedOpen } = useAnimatedToggle(open)

  if (isOpen) {
    return (
      <Popover
        className={cn(
          ANIMATED_POPOVER_STYLE,
          isAnimatedOpen ? 'cf-animated-open' : '',
          className
        )}
        {...otherProps}
      />
    )
  }

  return null
}

const Popover: FC<PopoverCbProps> = ({
  children,
  className,
  ...otherProps
}) => {
  const { popover, config } = usePopover(otherProps)
  const popoverCls = useMemo(() => {
    return createRootStyles(config)
  }, [config])

  popover.className = cn(popoverCls, className)

  return createPortal(children, popover)
}

export default PopoverHOC(Popover)
